


// Key Value float tuple
export class FloatKeyValue {
 
    // Key of tuple 
    public key: number;
 
    // Value of tuple 
    public value: number;
 
     constructor(key?: number, value?: number) { 
        if (key !== undefined) { this.key = key; }
        if (value !== undefined) { this.value = value; }
    }

}


