


// ZScore parameters
export class ZScore {
 
    // Mean value 
    public mean: number;
 
    // Standard Deviation 
    public sD: number;
 
     constructor(mean?: number, sD?: number) { 
        if (mean !== undefined) { this.mean = mean; }
        if (sD !== undefined) { this.sD = sD; }
    }

}


