


// String Int Value tuple
export class StringIntValue {
 
    // Key of tuple 
    public key: string;
 
    // Value of tuple 
    public value: number;
 
     constructor(key?: string, value?: number) { 
        if (key !== undefined) { this.key = key; }
        if (value !== undefined) { this.value = value; }
    }

}


