


// Key Value string tuple
export class StringKeyValue {
 
    // Key of tuple 
    public key: string;
 
    // Value of tuple 
    public value: string;
 
     constructor(key?: string, value?: string) { 
        if (key !== undefined) { this.key = key; }
        if (value !== undefined) { this.value = value; }
    }

}


